.games
{
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    background: url('../images/animais-de-exploração-agrícola-com-carneiros-41997646.webp');
}
.GBar
{
    gap: 20px;
    color: white;
    box-shadow: 0px 3px 20px -1px black;
    height: 60px;
}

.gamesLists
{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    margin: 10px;
    padding: 1px;
    margin-top: 50px;

}
.game
{
    background-color: white;
    margin: 1px;
    gap: 10px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 120px;
    height: 150px;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px black;
    
}
.game img {
    height: 100px;
    width: 115px;
}
.lvl
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 90px;
    width: 120px;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px black;
    position: relative;
}
.lvl p
{
    font-size: 30px;
    font-weight: bold;
    color: white;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.lvl img
{
    height: 20px;
}

.levels
{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
    margin-left: 10px;
    margin-top: 10px;
}
#chooseLVL
{
    text-align: center;
    color: white;
    font-size: 20px;
}
.lockedLVL
{
    height: 10px;
    width: 100%;
    rotate: 143deg;
    position: absolute;
    background-color: red;
}


.lvlGaming
{
    height: 100%;
    background:url('../images/animais-de-exploração-agrícola-com-carneiros-41997646.webp');
}
.timeBar
{
    height: 30px;
    width: 95%;
    background-color: gray;
    margin-left: 5px;
    border-radius: 10px;
    position: relative;
}
.timeText
{
    position: absolute;
    left: 45%;
    font-size: 20px;
    font-weight: bold;
}
.timeRest
{
    background-color: white;
    height: 30px;
    border-radius: 10px;


}
.playDeatils img
{
    height: 30px;
    width: 30px;
}
.playDeatils
{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.GamingBar
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    width: 98%;
}


.GamingBar img
{
    height: 30px;
    width: 40px;
}
 #help
{
    font-size: 25px;
    margin-right: 10px;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gamingModal
{
    height: 400px;
    width: 93%;
    position: relative;
    left: 5%;
    top: 2%;
    background-color: white;
    border-radius: 10px;
    box-shadow: -2px 11px 22px 1px black;
}

.animeModal
{
    animation: modaling 1.5s ease;
    transition: all ease linear;
}
@keyframes modaling {

    from

    {
        height: 0px;
        width: 0;
        top: 25%;
        left: 25%;
    }
    
}
#questionPart
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#questionPart h4
{
    font-size: 25px;
    text-align: center;
}
#questionPart img {
    height: 74px;
    width: 69px;
}
.letters
{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-left: 10px;
    margin-top: 20px;
    position: relative;
}

.letter
{
    position: relative;
}
.letters .letter
{
    height: 81px;
    width: 115px;
    border-radius: 20px;
    border: 1px black solid;
    box-shadow: 1px 1px 1px 1px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.letter
{
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
    font-size: 31px;

}
.letters div:nth-child(2)
{
    color: blue;
}
.letters div:nth-child(3)
{
    color: maroon;
}
.letters div:first-child 
{
    color: red;
}
.letters div:last-child 
{
    color: green;
}
/* .letters .letter:hover
{
    background-color: burlywood;
} */
.resultBox
{
    position: absolute;
   top: -5px;
   right: -5px;
   height: 35px;
   width: 35px;
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 50%;
animation: popins 0.3s ease;
}
@keyframes popins {
    0%
    {
        /* opacity: 0;
        height: 30px;
        width: 30px; */

    }
    50%
    {
        right: 4px;
    }

}
.good
{
    background-color: green;
   
}

.bad
{
    background-color: red;
}


.gameOver
{
    position: absolute;
    top: 0;
    z-index: 3;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.45);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.imageOver img
{
    height: 129px;
    width: 93px;
}
.gameIsOverModal
{
    height: 300px;
    width: 90%;
    background-color: white;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    padding-top: 10px;
    gap: 10px;
    animation: showingOver 0.5s ease;
}
@keyframes  showingOver
{
    from
    {
        opacity: 0;
    }
}

.gameIsOverModal h4
{
    text-align: center;
    font-size: 25px;
}
.actionsOvers
{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.actionsOvers button
{

    border: 0;
    width: 100px;
    height: 50px;
    padding: 5px;
    font-size: 18px;
    border-radius: 7px;
    color: white;
}
.actionsOvers div:nth-child(1) button
{
    background-color: green;
}
.actionsOvers div:nth-child(2) button
{
    background-color: red;
}

.winLvl button
{
    width: 75px;
    height: 47px;
    font-size: 17px; 
}
.winLvl  div:nth-child(3) button
{
    background-color: gray;
}

.starLvlWin img
{
    height: 30px;
    width: 40px;
}
#aplImg
{
    height: 127px;
    width: 138px;
}
.startG
{
    height: 215px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}
.startG button
{
    background-color: red;
}
.startG .GIcon
{
    height: 60px;
    width: 60px;
    background-color: green;
    border-radius: 50%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.startG .GIcon img
{
    height: 50px;
    width: 50px;
    margin-left: 5px;
}




